import { notFound } from "next/navigation"
import Link from "next/link"
import { sanityClient } from "@/lib/sanity"
import BlogHeader from "@/components/BlogHeader"
import Footer from "@/components/footer"
import { PortableText } from "@portabletext/react"
import { portableTextComponents } from "@/lib/portableTextComponents"

/* ----------------------------------------
   SEO METADATA
---------------------------------------- */
export async function generateMetadata({
  params,
}: {
  params: Promise<{ slug: string }>
}) {
  const { slug } = await params

  if (!slug) {
    return {
      title: "Blog | Cloud101",
      description: "Salesforce and Cloud learning articles by Cloud101.",
    }
  }

  try {
    const post = await sanityClient.fetch(
      `*[_type=="post" && slug.current==$slug][0]{
        title,
        excerpt
      }`,
      { slug }
    )

    if (!post) {
      return {
        title: "Blog | Cloud101",
        description: "Salesforce and Cloud learning articles by Cloud101.",
      }
    }

    return {
      title: `${post.title} | Cloud101`,
      description: post.excerpt?.slice(0, 155),
      alternates: {
        canonical: `https://cloud101.in/blog/${slug}`,
      },
    }
  } catch (err) {
    console.error("Metadata error:", err)
    return {
      title: "Blog | Cloud101",
      description: "Salesforce and Cloud learning articles by Cloud101.",
    }
  }
}

/* ----------------------------------------
   PAGE
---------------------------------------- */
export default async function BlogPostPage({
  params,
}: {
  params: Promise<{ slug: string }>
}) {
  const { slug } = await params

  if (!slug) {
    notFound()
  }

  const post = await sanityClient.fetch(
    `*[_type=="post" && slug.current==$slug][0]{
      title,
      excerpt,
      publishedAt,
      mainImage{ asset->{url} },
      categories[]->{title},
      author->{ name },
      content
    }`,
    { slug }
  )

  if (!post) {
    notFound()
  }

  return (
    <>
      <BlogHeader />

      <main className="max-w-4xl mx-auto px-5 py-16 pt-20">
        <Link
          href="/blog"
          className="inline-flex items-center gap-2 text-sm text-blue-600 hover:underline mb-6"
        >
          ‚Üê Back to Blog
        </Link>

        {/* Categories */}
        <div className="flex flex-wrap gap-2 mb-4">
          {post.categories?.map((cat: any) => (
            <span
              key={cat.title}
              className="text-xs font-medium bg-blue-50 text-blue-600 px-3 py-1 rounded-full"
            >
              {cat.title}
            </span>
          ))}
        </div>

        {/* Title */}
        <h1 className="text-4xl md:text-5xl font-bold mb-6">
          {post.title}
        </h1>

        {/* Excerpt */}
        {post.excerpt && (
          <p className="text-lg mb-10 border-l-4 border-blue-600 pl-4">
            {post.excerpt}
          </p>
        )}

        {/* Featured Image */}
        {post.mainImage?.asset?.url && (
          <img
            src={post.mainImage.asset.url}
            alt={post.title}
            className="w-full rounded-3xl mb-14"
          />
        )}

        {/* Content */}
        <article className="prose prose-lg max-w-none">
          <PortableText
            value={post.content}
            components={portableTextComponents}
          />
        </article>

        {/* CTA SECTION (kept) */}
        <section className="bg-gradient-to-r from-blue-50 to-blue-100 py-12 mt-16 rounded-3xl">
          <div className="text-center px-6">
            <h2 className="text-2xl sm:text-3xl font-bold text-gray-900 mb-4">
              Ready to Master Salesforce?
            </h2>
            <p className="text-gray-600 mb-6 max-w-2xl mx-auto">
              Join our next batch and get hands-on training from industry experts.
            </p>
            <a
              href="/#contact"
              className="inline-block px-8 py-3 bg-[#0B66FF] text-white rounded-full font-semibold hover:bg-[#0546C8] transition-colors"
            >
              Enroll Now
            </a>
          </div>
        </section>
      </main>

      <Footer />
    </>
  )
}